import "./consiglio.js";
import { Pagina } from "../../commons/js/pagina.js";
import { Menu, MenuItem, MenuModel } from '../../commons/js/menu.js';

$(document).ready(function () {

    var urlParams = new URLSearchParams(window.location.search);
    const idOrganismo = urlParams.get('organismo');
    const idSezione = urlParams.get('sezione');
    const idCorrelata = urlParams.get('correlata');

    if (idOrganismo != null && idSezione != null) {
        renderContenuto(function () {
            renderNavigazione();
            renderNavigazioneCorrelata();
        });
    } else {
        window.location = "notFound.html";
    }

    function renderNavigazione() {
        var urlOrganismo = apibase.baseurl + "oi/10400/" + idOrganismo;
        $.ajax({
            url: urlOrganismo,
            success: function (organismo) {
                if (organismo) {
                    const urlOrganismo = 'pagina-organismo.html?id=' + organismo.id;
                    const breadCrumb = '<a href="' + urlOrganismo + '">' + organismo.title + '</a><span class="separator">/</span>';
                    $('#breadcrumbOrganismo').html(breadCrumb);
                    $('#headingSubItem').html(organismo.title);
                    const placeholder = "https://via.placeholder.com/760x280/006BAC/ffffff?text=" + organismo.title;
                    let bannerSrc = placeholder;
                    if (organismo.components.banner && organismo.components.banner.components && organismo.components.banner.components.length) {
                        const banner = organismo.components.banner;
                        bannerSrc = baseImgUrl + banner.components[0].properties.address;
                    }
                    $('#bannerOrganismo').html('<img width="760" height="280" src="' + bannerSrc + '" class="img-fluid" >');
                    renderMenuOrganismo(organismo.title);
                }
            }, error: function (res_err) {
                window.location = "notFound.html";
            }
        });
    }

    function renderMenuOrganismo(title) {
        var urlMenu = apibase.baseurl + 'oi/10400/' + idOrganismo + '/menu';
        return $.ajax({
            url: urlMenu,
            success: function (menu) {
                let menuItems = [];
                menu.forEach(voce => {
                    const link = 'sottopagina-organismo.html?organismo=' + idOrganismo + '&sezione=' + voce.id;
                    const menuItem = new MenuItem(voce.title, link);
                    if (voce.id == idSezione) {
                        menuItem.active = true;
                    }
                    menuItems.push(menuItem);
                    if (voce.id == idCorrelata) {
                        const url = 'sottopagina-organismo.html?organismo=' + idOrganismo + '&sezione=' + voce.id;
                        const breadCrumb = '<a href="' + url + '">' + voce.title + '</a><span class="separator">/</span>';
                        $('#breadcrumbOrganismo').append(breadCrumb);
                    }
                });
                if (menuItems.length) {
                    const menuModel = new MenuModel(title, menuItems);
                    const menuHtml = Menu.render(menuModel);
                    $('#subMenu').html(menuHtml);
                }
            }
        });
    }

    function renderNavigazioneCorrelata() {
        var urlMenu = apibase.baseurl + 'oi/10401/' + idCorrelata;
        return $.ajax({
            url: urlMenu,
            success: function (pagina) {
                renderMenuCorrelata(pagina, pagina.title);
            }
        });
    }

    function renderMenuCorrelata(sezione, title) {
        if (sezione.components.pagina) {
            let menuItems = [];
            sezione.components.pagina.components.forEach(pagina => {
                const link = 'sottopagina-organismo.html?organismo=' + idOrganismo + '&sezione=' + pagina.id + '&correlata=' + idCorrelata;
                const menuItem = new MenuItem(pagina.title, link);
                if (pagina.id == idSezione) {
                    menuItem.active = true;
                }
                menuItems.push(menuItem);
            });
            if (menuItems.length) {
                const menuModel = new MenuModel(title, menuItems);
                const menuHtml = Menu.render(menuModel);
                $('#menuCorrelate').html(menuHtml);
            }
        }
    }

    function renderMenuSezione(sezione) {
        if (!urlParams.has('correlata') && sezione.components.pagina) {
            let menuItems = [];
            sezione.components.pagina.components.forEach(pagina => {
                const link = 'sottopagina-organismo.html?organismo=' + idOrganismo + '&sezione=' + pagina.id + '&correlata=' + idSezione;
                const menuItem = new MenuItem(pagina.title, link);
                menuItems.push(menuItem);
            });
            if (menuItems.length) {
                const menuModel = new MenuModel(sezione.title, menuItems);
                const menuHtml = Menu.renderCollapse(menuModel, 'collapseMenuSezione');
                $('#menuSezione').html(menuHtml);
            }
        }
    }

    function renderContenuto(done) {
        var urlSezione = apibase.baseurl + 'oi/10401/' + idSezione;
        return $.ajax({
            url: urlSezione,
            success: function (result) {
                let breadcrumb = '<li class="breadcrumb-item">' + result.title + '</li>';
                $('#bread').append(breadcrumb);
                $("#title").text(result.title);
                $("#container_base").append(Pagina.renderIntestazione(result));
                $("#container_base").append(Pagina.renderImmagine(result));
                $("#container_base").append(result.properties.testo || '');
                $("#container_base").append(Pagina.renderCartelle(result));
                if (urlParams.has('correlata')) {
                    $("#container_base").append(renderPagineCorrelate(result));
                }
                $("#container_base").append(Pagina.renderAllegati(result));
                $("#container_base").append(Pagina.renderLink(result));
                $("#container_base").append(Pagina.renderPieDiPagina(result));
                renderMenuSezione(result);
                if (done) { done() };
            }, error: function (res_err) {
                window.location = "notFound.html";
            }
        });
    }

    function renderPagineCorrelate(result) {
        if (result.components.pagina && result.components.pagina.components.length) {
            const pagine = result.components.pagina.components;
            const title = result.components.pagina.label;
            let accordion = '';
            pagine.forEach(pagina => {
                accordion += ' \
                    <div class="collapse-header" id="heading' + pagina.id + '">\
                        <button class="collapsed" data-toggle="collapse" data-target="#accordion' + pagina.id + '" aria-expanded="false" aria-controls="accordion' + pagina.id + '">' + pagina.title + '</button>\
                    </div>\
                    <div id="accordion' + pagina.id + '" class="collapse hide" role="tabpanel" aria-labelledby="heading' + pagina.id + '" data-parent="#accordionCorrelate">\
                        <div class="collapse-body">' +
                    Pagina.renderImmagine(pagina) +
                    (pagina.properties.testo || '') +
                    Pagina.renderCartelle(pagina) +
                    Pagina.renderAllegati(pagina) +
                    Pagina.renderLink(pagina) +
                    '</div>\
                    </div>';
            });
            return '\
                <hr class="border-news mb-0"/>\
                <div class="row mb-4">\
                    <h6 class="dark-obj-name ml-2 mt-2">' + title + '</h6>\
                </div>\
                <div id="accordionCorrelate" class="collapse-div" role="tablist">' + accordion + '</div>';
        }
        return '';
    }

})