import "./conf.js";

export const MenuModel = function (title, items, options) {
    this.title = title || '';
    this.items = items || [];
    this.options = options || {};
}

export const MenuItem = function (label, link, target) {
    this.label = label;
    this.link = link;
    this.target = target || '_self';
    this.active = false;
}

var MenuComponent = function () {
};

MenuComponent.prototype.render = function (menuModel) {
    let menu = '<h1 class="head_side_menu title_menu_right">' + menuModel.title + '</h1><ul class="link-list">';
    for (const item of menuModel.items) {
        const style = item.active ? ' active ' : '';
        const itemHtml = '\
            <li class="side_menu">\
                <div class="div-menu">\
                    <a class="list-item-a' + style + '" href="' + item.link + '" target="' + item.target + '">\
                        <span>' + item.label + '</span>\
                    </a>\
                </div>\
            </li>';
        menu += itemHtml;
    }
    menu += '</ul>';
    return menu;
};

MenuComponent.prototype.renderCollapse = function (menuModel, menuId) {
    let menu = '<div class="collapse-header">\
                    <button data-toggle="collapse" data-target="#' + menuId + '" aria-expanded="true" aria-controls="' + menuId + '" id="buttonSubItem" class="">' + menuModel.title + '</button>\
                </div>\
                <div id="' + menuId + '" role="tabpanel" aria-labelledby="" class="collapse show" style="">\
                    <ul class="bg-dark-blue" style="padding-right: 1em;">';
    for (const item of menuModel.items) {
        const style = item.active ? ' active ' : '';
        const itemHtml = '\
            <li class="side_menu">\
                <div class="div-menu">\
                    <a class="list-item-a' + style + '" href="' + item.link + '" target="' + item.target + '">\
                        <span>' + item.label + '</span>\
                    </a>\
                </div>\
            </li>';
        menu += itemHtml;
    }
    menu += '</ul>';
    menu += '</div>';
    return menu;
};


export const Menu = new MenuComponent();
